
// Imports
import style from './style.scss';
import default_state from './imports/state';

let gen_Div = (id, classname, data) => {

    let element = document.createElement('div');

    if (id != null) {
      element.setAttribute("id",id);
    };

    if (id != null) {
      if (classname) {
          element.classList = classname;
      };
    };
                        
    if ((data) != null) {
        element.innerHTML = `
            ${data}
        `;
    };

    return element
};

let state;
let dev;
let buffer;
